import React from 'react';
import './Post.css';
import VerifiedStamp from '../src/images/verified.svg'
import { Avatar } from '@material-ui/core';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import RepeatIcon from '@material-ui/icons/Repeat';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import PublishIcon from '@material-ui/icons/Publish';

function Post({
    displayName,
    username,
    verified,
    timestamp,
    text,
    image,
    avatar,
    retweet,
    like,
    share,
    comment,
}) {
    return (
        <div className="post">
            <div className="post__avatar">
                <Avatar src="https://pbs.twimg.com/profile_images/1254238355304390659/ktFPUsWS_bigger.jpg" />
            </div>
            <div className="post__body">
                <div className="post__header">
                    <div className="post__headerText">
                        <h3>#100daysofcode - StaFa â„¢ <span className="post__headerSpecial"><img src={VerifiedStamp} alt="" height="17" width="17" /> @stafa_san</span></h3>
                    </div>
                </div>
                <div className="post__headerDescription">
                    <p>I am buidling a Twitter Clone</p>
                </div>
                <img src="https://media.giphy.com/media/clspXK4twFiGjTwxOZ/giphy.gif" alt="" /> 
                <div class="post__footer">
                    <ChatBubbleOutlineIcon fontSize="small" />
                    <RepeatIcon fontSize="small" />
                    <FavoriteBorderIcon fontSize="small" />
                    <PublishIcon fontSize="small" />
                </div>
            </div>                
        </div>
    )
}

export default Post
